.on-submit-btn{
    background-color: #f9f9f9;
    border: 2px solid #e3e5e4;
    width: 80px;
    padding: 2px 5px;
    color: #666666;
    font-size: 12px;
    margin-top: 20px;
    text-align: center;
    cursor: pointer;
}
.on-submit-btn:active{
    opacity: 0.8;
}
.limit-text{
    margin-left: 10px;
    margin-right: 10px;
    cursor: pointer;
    color: #aaa;
}
.limit-text.select{
    color: red;
}